.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.map-container {
  height: 100vh;
}

.top_left {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 6px 12px;
  font-family: monospace;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  border-radius: 4px;
}

@media only screen and (min-width: 750px) {
  .top_right {
    background-color: rgba(0, 0, 0, 0.2);
    color: #fff;
    padding: 6px 12px;
    font-family: monospace;
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    margin: 12px;
    border-radius: 4px;
    width: 15em;
    max-height: 23em;
  }
}

@media only screen and (max-width: 750px) {
  .top_right {
    display: none;
  }
}


.inner_content {
  overflow: scroll;
  max-height: 16em;
}

.action_button {
  margin-left: 2em;
}

.mapboxgl-popup-content {
  border-radius: 1em;
  background: black;
  opacity: 0.75;
  color: white;
}

.mapboxgl-popup-close-button {
  display: none;
}

.user-container {
  margin-bottom: 0.5em;
  cursor: pointer;
}

.user-container:last-child {
  margin-bottom: 0;
}
/* The overlay effect with black background */
.overlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  background-color: rgba(
    0,
    0,
    0,
    0.7
  ); /* Black with a little bit see-through */
}

/* examples/custom-styling.css */
.customOverlay {
  background: rgba(0, 0, 0, 0.5);
}
.customModal {
  background: #000000;
  width: 75%;
  color: azure;
  border-radius: 0.5em;
}
